const axios = require('axios')

function setType(object, type) {
  Object.defineProperty(object, '_ethersType', {
    configurable: false,
    value: type,
    writable: false
  })
}

function isType(object, type) {
  return object && object._ethersType === type
}

function defineReadOnly(object, name, value) {
  Object.defineProperty(object, name, {
    enumerable: true,
    value: value,
    writable: false
  })
}

class Signer {
  constructor(provider, getIdToken) {
    // PROPS
    setType(this, 'Signer')
    defineReadOnly(this, 'provider', provider)

    // AXIOS
    this.vaultX = axios.create({
      baseURL: 'https://x0h8dsw2je.execute-api.us-east-1.amazonaws.com/production'
    })
    this.vaultX.interceptors.request.use(
      async config => {
        config.headers.authorization = `Bearer ${await getIdToken()}`
        return config
      },
      error => Promise.reject(error)
    )
  }

  isSigner(value) {
    return isType(value, 'Signer')
  }

  async getAddress() {
    try {
      const result = await this.vaultX.get('/address')
      return result.data.data
    } catch (err) {
      throw err
    }
  }

  async sign(tx) {
    try {
      const result = await this.vaultX.post('/signTransaction', tx)
      return result.data.data
    } catch (err) {
      throw err
    }
  }

  async signMessage(message) {
    try {
      // if (typeof message === 'string') {
      //   message = ethers.utils.toUtf8Bytes(message)
      // }

      // let messageHex = ethers.utils.hexlify(message).substring(2)
      const result = await this.vaultX.post('/signPersonalMessage', {
        data: message
      })
      return result.data.data
    } catch (err) {
      throw err
    }
  }

  async sendTransaction(transaction) {
    transaction.nonce =
      transaction.nonce || (await this.provider.getTransactionCount(await this.getAddress()))
    const signedTx = await this.sign(transaction)
    const txHash = await this.provider.sendTransaction(signedTx)
    return txHash
  }
}

module.exports = Signer
