const axios = require('axios')
const createPayload = require('web3-provider-engine/util/create-payload.js')
const Subprovider = require('./subprovider.js')

class RpcSource extends Subprovider {
  constructor({ rpcAuth, rpcUrl, ordersServiceUrl, getIdToken, appId, identity }) {
    super()
    this.appId = appId
    this.identity = identity
    this.rpcUrl = (rpcAuth && rpcAuth.url) || rpcUrl
    if (!this.rpcUrl) throw new Error('RpcSource: rpcUrl is undefined')
    this.getIdToken = getIdToken
    this.ordersServiceUrl = ordersServiceUrl
  }

  async handleRequest(payload, next, end) {
    let newPayload = createPayload(payload)
    this.idToken = await this.getIdToken()

    if (payload.method === 'eth_sendRawTransaction') {
      newPayload = payload.params[0]
      newPayload.jsonRpcReponse = true
      newPayload.id = payload.id
    }

    const headers = { Accept: 'application/json', 'Content-Type': 'application/json' }

    const requestParams = {
      url:
        payload.method === 'eth_sendRawTransaction'
          ? this.ordersServiceUrl + '/order'
          : this.rpcUrl,
      method: 'post',
      headers:
        payload.method === 'eth_sendRawTransaction'
          ? Object.assign(headers, { Authorization: 'Bearer ' + this.idToken })
          : headers,
      data: newPayload
    }

    axios(requestParams)
      .then(res => {
        if (payload.method === 'eth_getTransactionReceipt') {
          if (res.data.result === null || res.data.result.blockHash === null) {
            return end(null, null)
          }
        }
        return end(null, res.data.result)
      })
      .catch(error => {
        console.log(error)
        return end(new Error(error.response.data.message))
      })
  }
}

module.exports = RpcSource
