'use strict'

const axios = require('axios')

class EventEmitter {
  constructor () {
    this.events = {}
  }
  on (event, listener) {
    if (typeof this.events[event] !== 'object') {
      this.events[event] = []
    }
    this.events[event].push(listener)
    return () => this.removeListener(event, listener)
  }
  removeListener (event, listener) {
    if (typeof this.events[event] === 'object') {
      const idx = this.events[event].indexOf(listener)
      if (idx > -1) {
        this.events[event].splice(idx, 1)
      }
    }
  }
  emit (event, ...args) {
    if (typeof this.events[event] === 'object') {
      this.events[event].forEach(listener => listener.apply(this, args))
    }
  }
  once (event, listener) {
    const remove = this.on(event, (...args) => {
      remove()
      listener.apply(this, args)
    })
  }
}

class ShiplID {
  constructor ({ appId, client, authUrl, network }) {
    if (!appId) throw new Error('appId is missing')
    if (!client) throw new Error('client is missing')

    switch (client) {
      case 'vaultx':
        break
      case 'meta':
        break
      default:
        throw new Error(`client: ${client} is not available`)
    }
    this.events = new EventEmitter()
    this.shiplID = axios.create({
      baseURL: authUrl,
      headers: { 'Content-Type': 'application/json' }
    })
    this.auth = {
      userName: undefined, // MD5 of phoneNumber,
      userSub: undefined, // AWS UserSub uuid
      phoneNumber: undefined,
      identity: undefined,
      token: {
        RefreshToken: undefined,
        IdToken: undefined,
        AccessToken: undefined
      },
      session: undefined,
      challenge: undefined
    }
    this.appId = appId
    this.client = client
    this.network = network

    // BINDING
    this.logout = this.logout.bind(this)
    this.getIdToken = this.getIdToken.bind(this)
    this.login = this.login.bind(this)
    this.verify = this.verify.bind(this)
    this.isLogin = this.isLogin.bind(this)

    // INIT
    this.getAccessToken()
  }

  isLogin () {
    if (
      this.auth &&
      this.auth.identity &&
      this.auth.userName &&
      this.auth.token &&
      this.auth.token.IdToken
    ) { return true }
    return false
  }

  getAccessToken () {
    if (typeof window !== 'undefined') {
      if (window.localStorage.getItem('shiplID') != null) {
        this.auth = JSON.parse(window.localStorage.getItem('shiplID'))
        if (this.isLogin()) this.events.emit('login', this.auth)
      }
    }
  }

  setAccessToken () {
    this.auth.session = undefined
    this.auth.challenge = undefined
    if (this.isLogin()) this.events.emit('login', this.auth)
    if (typeof window !== 'undefined') {
      window.localStorage.setItem('shiplID', JSON.stringify(this.auth))
    }
  }

  async renewToken () {
    try {
      const result = await this.shiplID.post('/refreshToken', {
        username: this.auth.userName,
        refreshToken: this.auth.token.RefreshToken,
        client: this.client
      })
      if (result.message === 'User does not exist.') {
        this.logout()
        return false
      }
      this.auth = {
        ...this.auth,
        token: {
          ...this.auth.token,
          ...result.data.data.AuthenticationResult,
          CreatedAt: Date.now()
        }
      }
      this.setAccessToken()
      return this.auth
    } catch (error) {
      throw error.response.data.message
    }
  }

  async resendConfirmationCode (phoneNumber) {
    if (!phoneNumber) throw new Error('missing phoneNumber')

    try {
      return await this.shiplID.post('/resendConfirmationCode', {
        phoneNumber,
        client: this.client
      })
    } catch (error) {
      throw error.response.data.message
    }
  }

  /**
   * Use to logout a user
   */
  logout () {
    if (typeof window !== 'undefined') {
      window.localStorage.removeItem('shiplID')
    }
    this.auth = undefined
    this.events.emit('logout')
  }

  /**
   * Use to get the IdToken
   */
  async getIdToken () {
    if (this.auth && this.auth.token) {
      if (Date.now() >= this.auth.token.CreatedAt + this.auth.token.ExpiresIn * 1000) {
        await this.renewToken()
      }
      return this.auth.token.IdToken
    }
    return undefined
  }

  /**
   * Use to get the IdToken
   */
  getIdentity () {
    if (this.auth && this.auth.identity) {
      return this.auth.identity
    }
    return undefined
  }

  /**
   * Use to login a user or to sign up
   * @param {*} phoneNumber The phone number of the user
   * @param {*} deviceKey The address of the user, only madatory for meta registration / sign in
   */
  async login (phoneNumber, deviceKey) {
    if (this.client === 'meta' && !deviceKey) {
      throw new Error(`Your client is ${this.client}: deviceKey is missing`)
    }
    let result

    try {
      result = await this.shiplID.post('/login', {
        phoneNumber,
        client: this.client,
        appId: this.appId,
        username: deviceKey || '0x0'
      })
      this.auth = {
        ...this.auth,
        phoneNumber,
        userName: result.data.data.UserName,
        userSub: result.data.data.UserSub
      }
      if (result.data.data.Session) {
        this.auth.session = result.data.data.Session
        this.auth.challenge = result.data.data.ChallengeParameters.messageToSign
      }
      return this.auth
    } catch (error) {
      if (error.code === 'UserNotConfirmedException') {
        result = await this.resendConfirmationCode(phoneNumber)
      }
      throw error.response.data.message
    }
  }

  /**
   * Verify Identity of the user
   * @param {String} confirmationCode Should be a confirmation code (6 digits) or a signature
   */
  async verify (confirmationCode) {
    if (!confirmationCode) throw new Error('confirmation code is missing')
    switch (this.client) {
      case 'shiplID':
        break
      case 'meta':
        break
    }
    try {
      const result = await this.shiplID.post('/verify', {
        username: this.auth.userName,
        confirmationCode,
        appId: this.appId,
        session: this.auth.session,
        client: this.client
      })

      if (result.data.data.Session) {
        this.auth.session = result.data.data.Session
        return false
      }

      this.auth = {
        ...this.auth,
        token: { ...result.data.data.AuthenticationResult, CreatedAt: Date.now() }
      }

      const identity = await this.waitForIdentity(this.auth.userName, this.network)
      this.auth = {
        ...this.auth,
        token: { ...result.data.data.AuthenticationResult, CreatedAt: Date.now() },
        identity: identity
      }
      this.setAccessToken()

      return this.auth
    } catch (error) {
      throw error
    }
  }

  async waitForIdentity (username, network) {
    if (!username) throw new Error('missing deviceKey')
    if (!network) throw new Error('missing network')

    const timeout = 300000
    const interval = 5000
    const endTime = Number(new Date()) + timeout

    const checkCondition = async (resolve, reject) => {
      try {
        const result = await axios.get(`https://e2j9tkhdoi.execute-api.us-east-1.amazonaws.com/production/getIdentity/${network}/${username}`)
        const identity = result && result.data
        console.log('Check for Identity: ', identity)
        if (identity) {
          resolve(identity)
        } else if (Number(new Date()) < endTime) {
          setTimeout(checkCondition, interval, resolve, reject)
        } else {
          reject(new Error('Identity not found'))
        }
      } catch (error) {
        reject(error)
      }
    }
    return new Promise(checkCondition)
  }

  async lookupIdCreation (username, network) {
    if (!username) throw new Error('missing deviceKey')
    if (!network) throw new Error('missing network')

    const timeout = 300000
    const interval = 5000
    const endTime = Number(new Date()) + timeout

    const checkCondition = async (resolve, reject) => {
      try {
        const result = await this.shiplID.post('/lookup', { username, network })
        if (result.data.data === 'no record found') {
          resolve('no record found')
        } else if (
          result.data.data === 'null identity. Not mined yet?' &&
          Number(new Date()) < endTime) {
          setTimeout(checkCondition, interval, resolve, reject)
        } else {
          resolve(result.data.data)
        }
      } catch (error) {
        reject(error)
      }
    }
    return new Promise(checkCondition)
  }

  async exist (deviceKey) {
    if (!deviceKey) throw new Error('missing deviceKey')

    const result = await this.shiplID.post('/exist', { deviceKey })
    return result.data.data
  }
}

module.exports = ShiplID
