'use strict'

class ModalX {
  constructor() {
    this.button = undefined
    this.modal = undefined
    this.expertMode = false
    this.getAccessToken()
  }

  setExpertMode(expert) {
    this.expertMode = expert
    this.setAccessToken()
  }

  getAccessToken() {
    if (typeof window !== 'undefined') {
      if (window.localStorage.getItem('vaultx-expert-mode') != null) {
        this.expertMode = window.localStorage.getItem('vaultx-expert-mode')
      }
    }
  }

  setAccessToken() {
    if (typeof window !== 'undefined') {
      window.localStorage.setItem('vaultx-expert-mode', this.expertMode)
    }
  }

  formatMessage(method, payload, error) {
    if (!method) throw new Error("'formatMessage' method param is missing")
    return `vaultx~${JSON.stringify({ method, payload, error })}`
  }

  createOverlay() {
    const self = this
    const modalItem = function() {
      self.button = document.createElement('div')
      self.button.id = 'vaultx-button'
      self.button.innerHTML =
        '<img src="https://zupimages.net/up/19/16/ls6o.png" alt="" style="width: 40px; height: 40px; padding: 8px;">'
      self.button.style =
        'display: none; position: fixed; bottom: 20px; right: 20px; border: none; border-radius: 0; z-index: 2147483647; background: #17275d; border-radius: 50%;'
      document.body.appendChild(self.button)
      self.button.addEventListener('click', () => {
        self.showWallet()
      })

      const modalStyle = document.createElement('style')
      modalStyle.innerHTML =
        '\n  .vaultx-iframe {\n    display: none;\n    position: fixed;\n    top: 0;\n    right: 0;\n    width: 100%;\n    height: 100%;\n    border: none;\n    border-radius: 0;\n    z-index: 2147483647;\n  }\n'
      modalStyle.type = 'text/css'
      document.head.appendChild(modalStyle)

      self.modal = document.createElement('iframe')
      self.modal.className = 'vaultx-iframe'
      self.modal.id = 'vaultx-iframe'
      self.modal.src = 'http://vaultx-modal.surge.sh'
      document.body.appendChild(self.modal)
    }

    ;[('loaded', 'interactive', 'complete')].indexOf(document.readyState) > -1
      ? modalItem()
      : window.addEventListener('load', modalItem.bind(self))
  }

  show() {
    // if (!this.expertMode) {
    document.getElementById('vaultx-button').style.display = 'block'
    // }
  }

  hide() {
    document.getElementById('vaultx-button').style.display = 'none'
  }

  formatPostMessage(postMessageName, data) {
    const iframe = document.getElementById('vaultx-iframe')
    if (iframe === null) {
      this.createOverlay()
      const self = this
      this.modal.addEventListener('load', function() {
        this.contentWindow.postMessage(self.formatMessage(postMessageName, data), '*')
      })
    } else {
      iframe.contentWindow.postMessage(this.formatMessage(postMessageName, data), '*')
    }
  }

  getVerification(isRetry = false) {
    this.formatPostMessage('getVerification', { isRetry })
  }

  getLogin() {
    this.formatPostMessage('getLogin')
  }

  getConfirmation(data) {
    console.log('askConfirmation: ', data)
    this.formatPostMessage('getConfirmation', data)
  }

  initWallet(data) {
    this.formatPostMessage('wallet', data)
  }

  showWallet() {
    this.openOverlay()
    this.formatPostMessage('openWallet')
  }

  closeWallet() {
    this.openOverlay()
    this.formatPostMessage('closeWallet')
  }

  showLogin() {
    this.openOverlay()
    this.formatPostMessage('openLogin')
  }

  closeLogin() {
    this.openOverlay()
    this.formatPostMessage('closeLogin')
  }

  refreshKeys(keys) {
    this.formatPostMessage('refreshKeys', keys)
  }

  txPending(tx) {
    this.formatPostMessage('txPending', tx)
  }

  txConfirmed() {
    this.formatPostMessage('txConfirmed')
  }

  openOverlay() {
    const iframe = document.getElementById('vaultx-iframe')
    iframe.style.display = 'block'
  }

  closeOverlay() {
    const iframe = document.getElementById('vaultx-iframe')
    iframe.style.display = 'none'
  }
}

module.exports = ModalX
