const axios = require('axios') // replace per fetch window
const { utils, Contract, providers } = require('ethers')
const ProviderEngine = require('web3-provider-engine')
const CacheSubprovider = require('web3-provider-engine/subproviders/cache.js')
const FixtureSubprovider = require('web3-provider-engine/subproviders/fixture.js')
const FilterSubprovider = require('web3-provider-engine/subproviders/filters.js')
const HookedWalletSubprovider = require('web3-provider-engine/subproviders/hooked-wallet.js')
const SubscriptionSubprovider = require('web3-provider-engine/subproviders/subscriptions')
const HttpSubprovider = require('./services/httpSubprovider.js')
const VaultX = require('./services/vaultX')
const ShiplID = require('./services/shiplID')
const WalletMultisig = require('./contracts/WalletMultisig.json')

const engine = new ProviderEngine()

class Wallet {
  constructor({ appId, network, rpcUrl, masterAddress }) {
    this.appId = appId
    this.network = network
    this.rpcUrl = rpcUrl
    this.masterAddress = masterAddress
    this.confirmation = {
      approveMessage: false,
      approvePersonalMessage: false,
      approveTransaction: false,
      approveTypedMessage: false
    }

    switch (this.network) {
      case 'poa':
      case 'xdai':
      case 'mainnet':
        this.ordersServiceUrl = 'https://mainnet.api.shipl.co/orders'
        this.authUrl = 'https://mainnet.api.shipl.co/auth'
        this.vaultxUrl = 'https://3tgrlj7lf8.execute-api.us-east-2.amazonaws.com/production/'
        break
      default:
        this.ordersServiceUrl = 'https://testnet.api.shipl.co/orders'
        this.authUrl = 'https://testnet.api.shipl.co/auth'
        this.vaultxUrl = 'https://x0h8dsw2je.execute-api.us-east-1.amazonaws.com/production'
        break
    }
    this.provider = new providers.JsonRpcProvider(this.rpcUrl)
    this.shiplID = new ShiplID({
      authUrl: this.authUrl,
      appId: this.appId,
      client: 'vaultx',
      network: this.network
    })
    this.vaultX = new VaultX({
      rpcUrl: this.rpcUrl,
      vaultXUrl: this.vaultxUrl,
      authUrl: this.authUrl,
      appId: this.appId,
      network: this.network,
      provider: this.getWeb3Provider(),
      shiplID: this.shiplID
    })
  }

  static async create({ appId }) {
    if (!appId) throw new Error('appId is missing')
    try {
      const result = await axios.get(
        `https://y2xjczq6p8.execute-api.us-east-1.amazonaws.com/production/app/${appId}`
      )
      const wallet = new Wallet({
        appId,
        network: result.data.network,
        rpcUrl: result.data.rpcUrl,
        masterAddress: result.data.masterAddress
      })
      return wallet
    } catch (err) {
      console.log('Error on Initialize', err)
      throw err
    }
  }

  enable() {
    console.log('vaultX enabled')
  }

  logout() {
    this.vaultX.logout()
  }

  timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms))
  }

  async login() {
    await this.timeout(1000)
    return new Promise((resolve, reject) => {
      this.vaultX.modalX.getLogin()
      this.shiplID.events.once('login', auth => {
        auth.signerAddress = this.vaultX.address
        auth.proxyAddress = this.vaultX.proxyAddress
        resolve(auth)
      })
    })
  }

  wrapperConnected(handler, args, callback) {
    if (this.vaultX.isLogin()) {
      callback(handler)
    } else {
      this.vaultX.enqueue(handler, args, true, callback)
      this.vaultX.modalX.getLogin()
    }
  }

  wrapperConfirmation(handler, args, callback) {
    this.vaultX.enqueue(handler, args, false, callback)
    this.vaultX.modalX.getConfirmation(args)
  }

  getEthersSigner() {
    return this.vaultX.signer
  }

  getWeb3Provider() {
    try {
      engine.addProvider(
        new FixtureSubprovider({
          web3_clientVersion: 'ProviderEngine/v0.0.0/javascript',
          net_listening: true,
          eth_hashrate: '0x00',
          eth_mining: false,
          eth_syncing: true
        })
      )
      engine.addProvider(new CacheSubprovider())
      engine.addProvider(new FilterSubprovider())
      engine.addProvider(
        new HookedWalletSubprovider({
          getAccounts: async callback => {
            this.wrapperConnected(callback, [], async callback => {
              const address = await this.vaultX.getAccounts()
              callback(null, [this.shiplID.getIdentity(), address])
            })
          },
          // SIGNATURE
          signMessage: async (msgParams, cb) => {
            const signedData = await this.vaultX.signMessage(msgParams)
            cb(null, signedData.toString('hex'))
          },
          signPersonalMessage: async (msgParams, cb) => {
            const signedData = await this.vaultX.signPersonalMessage(msgParams)
            cb(null, signedData.toString('hex'))
          },
          signTransaction: async txParams => {
            const proxyAsWallet = new Contract(
              this.shiplID.getIdentity(),
              WalletMultisig.abi,
              this.provider
            )
            txParams.nonce = (await proxyAsWallet.nonce()).toNumber() + 1
            txParams.value = txParams.value || 0
            txParams.data = txParams.data || '0x00'
            txParams.type = 0
            const txHash = utils.solidityKeccak256(
              ['address', 'uint256', 'address', 'uint256', 'bytes32', 'uint256'],
              [
                this.vaultX.proxyAddress,
                txParams.type,
                txParams.to,
                txParams.value,
                utils.keccak256(txParams.data),
                txParams.nonce
              ]
            )

            const signatures = await this.vaultX.signMessage({ data: txHash })

            const params = {
              to: this.vaultX.proxyAddress,
              data: proxyAsWallet.interface.functions[
                'execute(uint256,address,uint256,bytes,uint256,bytes[])'
              ].encode([
                ...[txParams.type, txParams.to, txParams.value, txParams.data, txParams.nonce],
                [signatures]
              ]),
              metaNonce: txParams.nonce,
              blockchain: this.network,
              appId: this.appId
            }

            return null, params
          },
          signTypedMessage: async (msgParams, cb) => {
            const signedData = await this.vaultX.signTypedMessage(msgParams)
            cb(null, signedData.toString('hex'))
          },
          // APPROVEMENT
          approveMessage: async (msgParams, cb) => {
            if (this.confirmation.approveMessage) {
              this.wrapperConfirmation(cb, [msgParams], async (msgParams, callback) => {
                callback(null, true)
              })
            } else cb(null, true)
          },
          approvePersonalMessage: async (msgParams, cb) => {
            if (this.confirmation.approvePersonalMessage) {
              this.wrapperConfirmation(cb, [msgParams], async (msgParams, callback) => {
                callback(null, true)
              })
            } else cb(null, true)
          },
          approveTransaction: async (msgParams, cb) => {
            if (this.confirmation.approveTransaction) {
              this.wrapperConfirmation(cb, [msgParams], async (msgParams, callback) => {
                callback(null, true)
              })
            } else cb(null, true)
          },
          approveTypedMessage: async (msgParams, cb) => {
            if (this.confirmation.approveTypedMessage) {
              this.wrapperConfirmation(cb, [msgParams], async (msgParams, callback) => {
                callback(null, true)
              })
            } else cb(null, true)
          }
        })
      )

      const filterAndSubsSubprovider = new SubscriptionSubprovider()
      filterAndSubsSubprovider.on('data', (err, notification) => {
        engine.emit('data', err, notification)
      })
      engine.addProvider(filterAndSubsSubprovider)
      engine.addProvider(
        new HttpSubprovider({
          rpcUrl: this.rpcUrl,
          rpcAuth: this.rpcAuth,
          ordersServiceUrl: this.ordersServiceUrl,
          appId: this.appId,
          getIdToken: this.shiplID.getIdToken,
          identity: this.shiplID.getIdentity()
        })
      )
      engine.on('error', error => {
        console.error('Engine Error: ', error)
      })
      engine.start()
      return engine
    } catch (error) {
      console.log(error)
      throw error
    }
  }
}

module.exports = Wallet
